.singleSuggestion {
    display: flex;
    align-items: center;
    background-color: #fff;
    color: #31385a;
    border: 1px solid transparent;
    border-radius: 10px;
    cursor: pointer;
    position: relative;
    padding: 25px 20px;
}

.singleSuggestion .link {
    width: 100%;
    display: flex;
    align-items: center;
}

/* title */
.singleSuggestion:hover .title {
    color: #4661e6;
}

.singleSuggestion .title {
    width: fit-content;
    transition: .25s;
}

.singleSuggestion .description {
    color: #647196;
    margin: 10px 0px;
}

/* Category */
.category {
    width: fit-content;
    background-color: #f2f4fe;
    color: #4661e6;
    border-radius: 5px;
    font-weight: 500;
    cursor: pointer;
    padding: 5px 15px;
}

/* Likes */
.likes {
    align-self: flex-start;
    background-color: #f2f4fe;
    border: none;
    font-weight: 600;
    border-radius: 8px;
    transition: .25s;
    cursor: pointer;
    padding: 5px 10px;
    margin-right: 50px;
}

.likes>p {
    color: #31385a;
}

.likes:hover {
    background-color: #cfd7ff;
}

/* Comments */
.singleSuggestion .numberOfComments {
    margin-left: auto;
}

.numberOfComments {
    display: flex;
    gap: 5px;
}

.numberOfComments>p {
    color: #31385a;
    font-weight: 500;
}

/* ******************************* */
.votePlus {
    background-color: #4661e6;

}

.votePlus p {
    color: #fff;
}

.votePlus svg path {
    stroke: #fff;
}

.votePlus:hover {
    background-color: #4661e6;
    opacity: .75;
}

/* *************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .singleSuggestion {
        padding: 25px 20px 90px 20px;
    }

    .likes,
    .numberOfComments {
        position: absolute;
        bottom: 10px;
    }

    .numberOfComments {
        right: 20px;
        bottom: 20px;
    }
}