.editContainer {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 15px;
    padding-left: 75px;
    margin: 20px 0px 0px 0px;
}

.editInput {
    width: 70%;
    height: 100px;
    resize: vertical;
    padding: 10px 0px 0px 10px;
}

.editContainer .btns {
    min-height: 100px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.editContainer .btns button {
    height: 49px;
    width: 150px;
    border-radius: 5px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.editContainer .btns button span {
    color: #fff;
}

.editContainer .btns .updateBtn {
    background-color: #ad1fea;
    transition: .5s;
    font-size: .9rem;
}

.editContainer .btns .updateBtn:hover {
    opacity: .7;
}

/* *************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .editContainer {
        flex-direction: column;
        align-items: flex-end;
    }

    .editInput {
        width: calc(100% + 60px);
    }

    .editContainer .btns {
        min-height: initial;
        flex-direction: row;
        flex-wrap: wrap;
    }
}