.createContainer {
    max-width: 500px;
    margin: 0px auto;
    padding: 100px 0px 0px 0px;
}

/* header */
.createHeader {
    margin-bottom: 75px;
}

/* Form container */
.containerFormContainer {
    background-color: #fff;
    position: relative;
    border-radius: 10px;
    padding: 60px 30px 5px 30px;
    margin-bottom: 25px;
}

/* logo & title*/
.createLogo {
    width: 60px;
    position: absolute;
    top: -30px;
    left: 25px;
}

.containerFormContainer>.title {
    margin-bottom: 50px;
}

/* Form */
.createForm>div {
    position: relative;
    margin-bottom: 35px;
}

.createForm>div p {
    font-size: .9rem;
}

.createForm>div input,
.createForm>div textarea {
    width: 100%;
    height: 45px;
    font-size: 1rem;
    resize: vertical;
    margin-top: 10px;
    padding: 0px 0px 0px 10px;
}

.createForm>div textarea {
    height: 100px;
    padding: 10px 0px 0px 10px;
}

.createForm>div:nth-child(2) input,
.createForm>div:nth-child(3) input {
    cursor: pointer;
}

/* arrow down */
.arrowDown {
    position: absolute;
    right: 0;
    bottom: 18px;
}

/* Buttons */
.btns {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 15px;
}

.btns button {
    color: #fff;
}

.addBtn {
    min-width: 115px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.addBtn span {
    color: #fff;
}

.btns .cancelBtn {
    height: 49px;
    background-color: #3a4374;
    font-size: 1rem;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: .25s;
    padding: 0px 20px;
}

.btns .cancelBtn:hover {
    opacity: .7;
}

/* ******************** */
/* Media query */
@media screen and (min-width:765px) and (max-width:1100px) {
    .createContainer {
        padding: 50px 0px 0px 0px;
    }
}

@media screen and (min-width:0px) and (max-width:765px) {
    .createContainer {
        max-width: 95%;
        padding: 50px 0px 0px 0px;
    }

    .btns {
        flex-direction: column;
        gap: 10px;
    }

    .btns button,
    .btns .link {
        width: 100%;
    }

    .btns .link {
        order: 1;
    }

}