.empty {
    min-height: 450px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    justify-content: center;
    background-color: #fff;
    /* opacity: 0; */
    /* transform: translateY(25px); */
    border-radius: 10px;
}

.empty img {
    width: 20%;
    margin-bottom: 25px;
}

.empty>p {
    width: 80%;
    text-align: center;
}

.empty button {
    height: 49px;
    display: flex;
    align-items: center;
    margin-top: 25px;
}

/*********************************/
.show {
    transform: translateY(0px);
    opacity: 1;
    transition: .75s;
}