/* Buttons */
.btnsContainer {
    display: flex;
    justify-content: space-between;
}

.btnsContainer button {
    display: flex;
    justify-content: center;
    align-items: center;
}

.btnsContainer button span {
    color: #fff;
}

.deleteBtn {
    min-width: 83px;
    height: 49px;
    background-color: #d73737;
    color: #fff;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: .25s;
    padding: 0px 20px;
}

.deleteBtn:hover {
    opacity: .7;
}

/* ******************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .btnsContainer {
        flex-direction: column;
        gap: 10px;
    }

    .deleteBtn {
        order: 1;
    }
}