.replyContainer {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 15px;
    padding-left: 75px;
    margin: 20px 0px 0px 0px;
}

.replyInput {
    width: 70%;
    height: 100px;
    resize: vertical;
    padding: 10px 0px 0px 10px;
}

.replyContainer button {
    height: 40px;
    min-width: 86px;
    background-color: #ad1fea;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .9rem;
    border: none;
    border-radius: 5px;
    font-weight: 500;
    cursor: pointer;
    transition: .25s;
    padding: 0px 10px;
}

.replyContainer button span {
    color: #fff;
}

.replyContainer button:hover {
    opacity: .75;
}

/* ************ */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .replyContainer {
        flex-direction: column;
        align-items: flex-end;
    }

    .replyInput {
        width: calc(100% + 60px);
    }
}