.navbarContainer {
    flex: .25;
    background-color: transparent;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

/* Logo */
.navbarContainer .logo {
    min-height: 125px;
    background: url("../../public/assets/suggestions/desktop/background-header.png") center/cover no-repeat;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border-radius: 10px;
    line-height: 24px;
    padding: 0px 0px 10px 20px;
}

.icon {
    display: none;
}

.navbarContainer .logo .title {
    color: #fff;
    font-weight: 400;
}

.navbarContainer .logo .name {
    color: #fff;
}

.navbarContainer .filter,
.roadMap {
    display: block;
}


/* Filter */
.navbarContainer .filter,
.filterPhone {
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    padding: 25px;
    border-radius: 10px;
}

.navbarContainer .filter>p,
.filterPhone>p {
    background-color: #f2f4fe;
    color: #4661e6;
    border-radius: 5px;
    font-weight: 500;
    cursor: pointer;
    transition: .25s;
    padding: 5px 15px;
}

.navbarContainer .filter>p:hover,
.filterPhone>p:hover {
    background-color: #cfd7ff;
}

/* Roadmap */
.roadMap,
.roadMapPhone {
    background-color: #fff;
    color: #000;
    border-radius: 10px;
    padding: 20px;
}

.roadMap div,
.roadMapPhone div {
    display: flex;
    justify-content: space-between;
}

.roadMap>div:nth-child(1),
.roadMapPhone>div:nth-child(1) {
    display: flex;
    margin-bottom: 15px;
}

.roadMap>div:nth-child(2),
.roadMapPhone>div:nth-child(2) {
    flex-direction: column;
}

.viewLink {
    color: #4661e6;
    font-weight: 600;
    font-size: .9rem;
    text-decoration: underline;
}

.viewLink:hover {
    opacity: .7;
}

.dot {
    width: 6px;
    height: 6px;
    background-color: #e98888;
    border-radius: 50%;
    margin: auto;
    margin-right: 10px;
}

.inProgress .dot {
    background-color: #ad1fea;
}

.live .dot {
    background-color: #7c91f9;
}


.num {
    font-weight: 600;
}

/* NAV */
nav {
    display: none;
}

/* *********************** */
/* clicked styles */
.navbarContainer .filter .clicked,
.navbarContainer .filter .clicked:hover,
.navbarContainer .filterPhone .clicked,
.navbarContainer .filterPhone .clicked:hover {
    color: #fff;
    background-color: #4661e6;
}

/* ******************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .navbarContainer .logo {
        min-height: initial;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        border-radius: 0px;
        padding: 20px;
        z-index: 11;
    }

    .icon {
        display: flex;
        font-size: 1.5rem;
    }

    /* navbar */
    .navbarContainer .filter,
    .navbarContainer .roadMap {
        display: none;
    }

    nav {
        height: calc(100vh - 25px);
        display: block;
        position: absolute;
        top: 85px;
        right: 0;
        left: 0;
        background: transparent;
        z-index: -1;
        overflow: hidden;
    }

    .showNav {
        background: rgba(0, 0, 0, 0.5);
        z-index: 10;
    }

    .showNav>div {
        transform: translateX(0%);
    }

    nav>div {
        width: 75%;
        height: 100%;
        margin-left: auto;
        background-color: #f2f4fe;
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 20px;
        transition: .25s;
        transform: translateX(100%);
    }


}

@media screen and (min-width:765px) and (max-width:1100px) {
    .navbarContainer {
        flex-direction: row;
    }

    .navbarContainer>div {
        flex: 33.33%;
    }

    .navbarContainer .logo .name {
        font-size: 1.25rem;
    }
}