.Home {
    width: 100vw;
    max-width: 1100px;
    display: flex;
    gap: 35px;
    margin: 0px auto;
    padding: 75px 0px 0px 0px;
}

/* *************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .Home {
        max-width: 100%;
        flex-direction: column;
        gap: 0;
        padding: 0px 0px 0px 0px;
        position: relative;
    }
}

@media screen and (min-width:765px) and (max-width:1100px) {
    .Home {
        max-width: 95%;
        flex-direction: column;
        padding: 50px 0px 0px 0px;
    }
}