.roadmapContainer {
    width: 100%;
    max-width: 1050px;
    margin: 0px auto;
    padding: 75px 0px 0px 0px;
}

/* header */
.roadmapHeader {
    height: 100%;
    max-height: 70px;
    min-height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #3a4374;
    border-radius: 10px;
    padding: 10px 25px;
}

.roadmapHeader button {
    color: #fff;
    display: flex;
    align-items: center;
}

/* Title */
.roadmapHeader .title {
    color: #fff;
}

/* Back btn */
.backBtn {
    background-color: transparent;
    display: flex;
    align-items: center;
    gap: 10px;
    border: none;
    font-weight: 600;
    font-size: .9rem;
    cursor: pointer;
}

.roadmapHeader .backBtn p {
    color: #fff;
}

/* Add btn */
.roadmapHeader .addBtn {
    height: 49px;
}

/* ******************************************** */
/* navigation */
.navigation {
    display: flex;
    justify-content: space-between;
    margin: 30px 0px;
}

.navigation>div {
    flex: .32;
    pointer-events: none;
}

.navigation>div>p {
    font-size: .9rem;
}

/********************************************* */
/* Ideas */
.ideasContainer {
    display: flex;
    justify-content: space-between;
}

.ideasContainer>div {
    flex: .32;
    display: flex;
    flex-direction: column;
}

.ideasContainer>div>div {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    gap: 5px;
    border-top: 5px solid;
    border-radius: 10px;
    cursor: pointer;
    padding: 35px 20px 20px 20px;
    margin-bottom: 15px;
}

/* Title */
.ideasContainer>div .title {
    width: fit-content;
    transition: .25s;
}

/* status */
.ideasContainer .status {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 10px;
}

.ideasContainer .status>div:nth-child(1) {
    width: 6px;
    height: 6px;
    border-radius: 50%;
}

/* Category */
.ideasContainer .category {
    margin-top: 5px;
}

/* likes and comments */
.likesAndComments {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.likesAndComments button {
    align-items: center;
}

.likesAndComments .likes {
    width: 60px;
    height: 40px;
    display: flex;
    gap: 5px;
}

.likesAndComments .likes {
    width: 60px;
    height: 40px;
}

.likesAndComments .numberOfComments {
    background-color: transparent;
    border: none;
}

/* Dot and border and title styles */
/* planned */
.ideasContainer>div:nth-child(1)>div {
    border-color: #e98888;
}

.ideasContainer>div:nth-child(1)>div .status>div:nth-child(1) {
    background-color: #e98888;
}

.ideasContainer>div:nth-child(1)>div .title:hover {
    color: #e98888;
}

/* in-pregress */
.ideasContainer>div:nth-child(2)>div {
    border-color: #ad1fea;
}

.ideasContainer>div:nth-child(2)>div .status>div:nth-child(1) {

    background-color: #ad1fea;
}

.ideasContainer>div:nth-child(2)>div .title:hover {
    color: #ad1fea;
}

/* live */
.ideasContainer>div:nth-child(3)>div {
    border-color: #7c91f9;
}

.ideasContainer>div:nth-child(3)>div .status>div:nth-child(1) {
    background-color: #7c91f9;
}

.ideasContainer>div:nth-child(3)>div .title:hover {
    color: #7c91f9;
}

/* ************************************* */
.votePlus {
    background-color: #4661e6;

}

.votePlus p {
    color: #fff;
}

.votePlus svg path {
    stroke: #fff;
}

.votePlus:hover {
    background-color: #4661e6;
    opacity: .75;
}

/* ******************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .roadmapContainer {
        max-width: 100%;
        padding: 0px;
    }

    .roadmapHeader {
        max-height: 100px;
        min-height: 100px;
        border-radius: 0px;
    }

    /* navigation */
    .navigation {
        margin: 25px 0px 45px 0px;
    }

    .navigation>div {
        pointer-events: initial;
        text-align: center;
        padding-bottom: 10px;
        border-bottom: 5px solid transparent;
        cursor: pointer;
        opacity: .25;
    }

    .navigation>div>p {
        font-size: .65rem;
    }

    /* ideas */
    .ideasContainer {
        display: flex;
        justify-content: center;
    }

    .ideasContainer>div {
        flex: .95;
        flex-direction: column;
    }

    .ideasContainer>div:nth-child(2),
    .ideasContainer>div:nth-child(3) {
        display: none;
    }

    .ideasContainer>div>div {
        background-color: #fff;
        display: flex;
        flex-direction: column;
        gap: 5px;
        border-top: 5px solid;
        border-radius: 10px;
        cursor: pointer;
        padding: 35px 20px 20px 20px;
        margin-bottom: 15px;
    }

    /* likes and commets */
    .ideasContainer .likes,
    .ideasContainer .numberOfComments,
    .ideasContainer .numberOfComments {
        position: relative;
        bottom: 0px;
        right: 0px;
        bottom: 0px;
    }

    /* ************** */
    /* Javascript */
    .navigation .clicked:nth-child(1) {
        border-bottom: 5px solid #e98888;
        opacity: 1;
    }

    .navigation .clicked:nth-child(2) {
        border-bottom: 5px solid #ad1fea;
        opacity: 1;
    }

    .navigation .clicked:nth-child(3) {
        border-bottom: 5px solid #7c91f9;
        opacity: 1;
    }
}

@media screen and (min-width:765px) and (max-width:1100px) {
    .roadmapContainer {
        max-width: 95%;
        padding: 50px 0px 0px 0px;
    }

    .ideasContainer {
        font-size: 70%;
    }

    /* title */
    .ideasContainer>div .title {
        margin-bottom: 10px;
    }

    /* description */
    .description {
        max-width: 90%;
    }

    /* category */
    .ideasContainer .category {
        margin-top: 10px;
    }

    /* status */
    .ideasContainer .status {
        gap: 10px;
        margin-bottom: 20px;
    }
}