.commentsContainer {
    background-color: #FFF;
    border-radius: 10px;
    padding: 40px 30px 10px 30px;
    margin-bottom: 25px;
}

/* title */
.commentsContainer .title {
    font-size: 1.25rem;
    font-weight: 600;
    margin-bottom: 30px;
}

.comment {
    border-bottom: 1px solid #cfd7ff;
    padding-bottom: 30px;
    margin-bottom: 40px;
}

/* user info */
.userInfo {
    display: flex;
    align-items: center;
    gap: 30px;
}

/* img */
.userImg {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

/* username and reply btn */
.nameAndReply {
    flex: 1;
}

.usernameAndReply {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.replyBtn {
    background-color: transparent;
    color: #4661e6;
    border: none;
    font-size: .9rem;
    font-weight: 600;
    cursor: pointer;
}

.replyBtn:hover {
    text-decoration: 2px underline;
}

/* message */
.message {
    word-wrap: break-word;
    padding: 10px 0px 0px 80px;
}


/* Replires */
.replies {
    border-left: 1px solid #cfd7ff;
    margin: 0px 0px 0px 25px;
    padding: 40px 0px 0px 20px;
}

.replies .userImg {
    width: 40px;
    height: 40px;
}

.reply {
    margin-bottom: 30px;
}

.replyTo {
    font-weight: 600;
    color: #ad1fea;
}

/* *************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .commentsContainer {
        padding: 20px 10px;
    }

    /* user info */
    .userInfo {
        display: flex;
        gap: 15px;
    }

    /* img */
    .userImg {
        width: 40px;
        height: 40px;
    }

    .message {
        word-wrap: break-word;
        padding: 10px 0px 0px 0px;
    }

    /* ************* */
    /* replies */
    .replies {
        margin: 10px 0px 0px 0px;
        padding: 10px 0px 0px 5px;
    }

    .replies .userImg {
        width: 30px;
        height: 30px;
    }
}