@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
    margin: 0;
    padding: 0;
}

:root {
    --main-back-clr: #f2f4fe;
    --button-danger-background-hover: #e98888;
    --button-danger-background: #d73737;
    --button-edit-background-hover: #7c91f9;
    --button-edit-background: #4661e6;
    --button-primary-background-hover: #c75af6;
    --button-primary-background: #ad1fea;
    --button-secondary-background-hover: #656ea3;
    --button-secondary-background: #3a4374;
    --button-text-color: #f2f4fe;
    --button-vote-button-background-hover: #cfd7ff;
    --h1-text-color: rgb(58 67 116 / 96%);
    --heading-text-color: #3a4374;
    --text-color: #647196;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: #f2f4fe;
    font-family: 'Jost', sans-serif;
    overflow-x: hidden;
}

.link {
    text-decoration: none;
}

button {
    font-family: inherit;
}

textarea,
input {
    background-color: #f2f4fe;
    font-family: inherit;
    font-size: 1rem;
    outline: none;
    border-radius: 10px;
    border: 1px solid transparent;
    resize: vertical;
}

textarea:focus,
input:focus {
    border: 1px solid #4661e6;
}

h1,
h2,
h3,
h4,
h5 {
    color: #31385a;
}

p,
span {
    color: #647196;
}


/* loading effect */
#block {
    pointer-events: none;
    animation: fade 1s infinite ease-in-out alternate;
}


@keyframes fade {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0.5;
    }
}

#err {
    pointer-events: none;
    animation: none;
    opacity: .5;
}

/* handling request  */
/* loading */
body .loading {
    pointer-events: none;
}

body .loading .loader {
    display: flex;
}

body .loading span {
    display: none;
}

/* error */
html body .inputError {
    border: 1px solid #d73737;
}