.categoryDrpdown {
    width: 100%;
    background-color: #fff;
    position: absolute;
    top: calc(100% + 10px);
    border-radius: 10px;
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, .2);
    z-index: 1;
    transform: scaleY(0) translateY(-75px);
    transform-origin: top;
    transition: .0s;
}

.categoryDrpdown p {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    padding: 12px 20px;
}

.categoryDrpdown p:hover span {
    color: #ad1fea;
    transition: .25s;
}

.categoryDrpdown p img {
    visibility: hidden;
}

.categoryDrpdown p:not(.last) {
    border-bottom: 1px solid #f2f4fe;
}

/* ************************* */
.showCategoryDropdown {
    transform: scaleY(1) translateY(0px);
    transition: .5s;
}

.categoryDrpdown .clicked img {
    visibility: visible;
}