.homeHeader,
.homeHeader * {
    display: flex;
    align-items: center;
}

.homeHeader {
    flex: 1;
    height: 100%;
    max-height: 70px;
    min-height: 70px;
    justify-content: space-between;
    background-color: #3a4374;
    border-radius: 10px;
    padding: 0px 15px;
    margin-bottom: 15px;
}

/* Logo */
.homeHeader .logo img {
    margin-right: 8px;
}

.homeHeader .title {
    color: #FFF;
    font-size: 1.25rem;
}

/* Filter */
.homeHeader .sort {
    position: relative;
    cursor: pointer;
    transition: .25s;
}

.homeHeader .sort:hover>p {
    transition: .25s;
    opacity: .5;
}

.homeHeader .sort>img {
    margin-left: 8px;
}

.homeHeader .sort>p {
    color: #FFF;
    font-size: .9rem;
}

.homeHeader .sort>p:nth-child(1) {
    font-weight: 300;
    margin-right: 15px;
}

.homeHeader .sort>p:nth-child(2),
.homeHeader .sort>img {
    cursor: pointer;
    font-weight: 600;
}

/* sort options */
.sortOptions {
    width: 250px;
    background-color: #FFF;
    position: absolute;
    top: 30px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
    padding: 0px 10px;
}

.sortOptions>p {
    width: 100%;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    padding: 12px 10px;
}

.sortOptions>p img {
    visibility: hidden;
}

.sortOptions>p:not(.last) {
    border-bottom: 1px solid #f2f4fe;
}

/* ADD btn */
.addBtn {
    height: 49px;
    background-color: #ad1fea;
    gap: 8px;
    font-weight: 500;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: .25s;
    padding: 0px 15px;
}

.addBtn .icon {
    transform: scale(1.25);
}

.addBtn p {
    color: #fff;
}

.addBtn:hover {
    background-color: #c75af6;
}

/* ******************** */
/* sort clicked styles */
.sortOptions>.clickedSort span {
    color: #ad1fea;
    font-weight: 600;
}

.sortOptions>.clickedSort img {
    visibility: visible;
}

/* *********** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .homeHeader {
        border-radius: 0;
        padding: 0px 10px;
    }

    .homeHeader .logo {
        display: none;
    }

    /* ADD BUTTON */
    .addBtn {
        font-size: .7rem;
    }

    .addBtn .icon {
        display: none;
    }

    /* SORT */

    .homeHeader .sort>img {
        margin-left: 5px;
    }

    .homeHeader .sort>p {
        font-size: .75rem;
    }

    .homeHeader .sort>p:nth-child(1) {
        margin-right: 5px;
    }

}