.suggestionContainer {
    width: 100vw;
    max-width: 800px;
    margin: 0px auto;
    padding: 75px 0px 0px 0px;
}

/* Header */
.suggestionHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
}

.suggestionHeader button {
    background-color: transparent;
    display: flex;
    align-items: center;
    gap: 10px;
    border: none;
    font-weight: 600;
    font-size: .9rem;
    cursor: pointer;
}

.backBtn {
    color: #656ea3;
    text-underline-offset: 2px;
}

.backBtn:hover {
    text-decoration: 2px underline;
}

.suggestionHeader .editBtn {
    background-color: #4661e6;
    color: #fff;
    border-radius: 10px;
    transition: .15s;
    padding: 15px 20px;
}

.suggestionHeader .editBtn:hover {
    opacity: .7;
}

/* Suggestion */
.mySuggestion {
    margin-bottom: 25px;
}

.mySuggestion .singleSuggestion p:not(.likes > p) {
    cursor: initial;
}

.mySuggestion .singleSuggestion:hover .title {
    color: #31385a;
    cursor: initial;
}

.mySuggestion .singleSuggestion {
    transform: translateY(0);
    opacity: 1;
}


/* *************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:1100px) {
    .suggestionContainer {
        max-width: 95%;
        padding: 50px 0px 0px 0px;
    }
}