.commentsInputContainer {
    background-color: #fff;
    border-radius: 10px;
    padding: 25px;
    margin-bottom: 25px;
}

/* Title */
.commentsInputContainer .title {
    margin-bottom: 30px;
}

/* Form */
.commentForm {
    width: 100%;
}

.commentForm textarea {
    width: calc(100% - 25px);
    height: 100px;
    padding: 15px 0px 0px 20px;
    margin-bottom: 25px;
}

.commentForm>div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.leftCharachter {
    font-size: .8rem;
}

.addCommentBtn {
    min-width: 133px;
    background-color: #ad1fea;
    display: flex;
    justify-content: center;
    border: none;
    border-radius: 10px;
    font-size: .9rem;
    font-weight: 600;
    cursor: pointer;
    transition: .15s;
    padding: 15px 20px;
}

.addCommentBtn span {
    color: #fff;
}

.addCommentBtn span {
    display: flex;
}

.addCommentBtn:hover {
    opacity: .7;
}

/* *************** */
/* Media query */
@media screen and (min-width:0px) and (max-width:765px) {
    .commentsInputContainer {
        padding: 20px 10px;
    }

    .commentForm textarea {
        margin-bottom: 10px;
    }
}